/** Media Queries **/
.barra-mobile {
  background-color: #2563EB;
  padding: 2rem 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .barra-mobile {
    display: none;
  }
}
.barra-mobile h1 {
  color: #FFFFFF;
  margin: 0;
}
.barra-mobile .menu img {
  width: 6rem;
  filter: invert(100%);
  cursor: pointer;
}
.barra-mobile.Proyectos {
  background-color: #0891B2;
}
.barra-mobile.Crear {
  background-color: #4338CA;
}
.barra-mobile.Perfil {
  background-color: rgb(0, 120, 0);
}

.barra {
  display: none;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #FFFFFF;
    padding: 1rem 2rem;
    box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, 0.4);
  }
}
.barra .cerrar-sesion {
  background-color: #134cca;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .barra .cerrar-sesion {
    width: auto;
  }
}
.barra .cerrar-sesion:hover {
  background-color: #0e3b9c;
  cursor: pointer;
}
.barra.Proyectos p span {
  color: #0891B2;
}
.barra.Proyectos .cerrar-sesion {
